.contact__container {
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact__title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-15);

}

.contact__info {
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px;
}

.contact__card {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.75rem;
    text-align: center;

}

.contact__card-icon {
    font-size: 20rem;
    color: var(--title-color);
    margin-bottom: var(--mb-025);

}

.contact__card-title,
.contact__card-data {
    font-size: var(---small-font-size);
}

.contact__card-title {
    font-weight: var(--font-medium);
}

.contact__card-data {
    display: block;
    margin-bottom: var(--mb-075);
}

.contact__button {
    color: var(--title-color);
    font-size: var(---small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}

.contact__button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.contact__button:hover .contact__button-icon {
    transform: translate(0.25rem);
}

.contact__card-icon {
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-025);
}

.contact__form {
    width: 360px;
}

.contact__form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 0, 0, 0.3);
    background: none;
    color: var(--text-color);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
}

.contact__form-tag {
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;

}

.contact__form-area {
    height: 10rem;

}

.contact__form-area textarea {
    resize: none;
}


.success-message {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 128, 0, 0.9); /* Green background color */
    color: white;
    padding: 20px;
    border-radius: 5px;
    z-index: 9999;
  }

/* =======  MEDIA QUERIES ====== */

/* ------for Large Devices ------ */
@media screen and (max-width: 992px) {
    .contact__container {
        column-gap: 3rem;
    }

}

/* ------for Medium Devices ------ */
@media screen and (max-width: 768px) {

    .contact__container {
        grid-template-columns: 1fr;
        row-gap: 3rem;

    }

    .contact__info {
        justify-content: center;
    }

    .contact__form {
        margin: 0 auto;
    }



}

/* ------for Tabs ------ */
@media screen and (max-width: 576px) {
    .contact__info {
        grid-template-columns: 1fr;
    }
    
    .contact__form {
        width: 345px;
    }
}

/* ------for Small Devices ------ */
@media screen and (max-width: 350px) {
    .contact__info {
        grid-template-columns: 1fr;
    }


    .contact__form {
        width: 280px;
    }
}